.button {
  border: none;
  border-radius: 100px;
  
  padding: 1rem;
  background-color: var(--clr-primary-800);
  color: var(--clr-text-heading);
  
  font-size: 1.5rem;

  transition-duration: var(--tm-button-transition);

  outline-style: solid;
  outline-color: var(--clr-accent-400);
  outline-offset: 0;
  outline-width: 0;
}

.button--white {
  background-color: var(--clr-text-heading);
  color: var(--clr-primary-900);
}

.button--secondary {
  background-color: var(--clr-secondary-400);
  
}

.button:focus-visible,
.button:hover {
  outline-width: var(--sz-outline-width);
  outline-offset: var(--sz-outline-width);
}